import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Button } from "semantic-ui-react";
import _map from "lodash/map";
import _head from "lodash/head";
import Empty from "../../common/Empty";
import Blog from "./Blog";
import PageTitle from "../../common/PageTitle";

const Container = styled.div`
  margin-top: 18px;
  width: 1300px;
  display: flex;
  justify-content: space-between;
`;

const MenusBox = styled.div`
  display: flex;
  flex-direction: column;
  width: 180px;
  padding-right: 40px;
`;

const Menus = ({ codes, onSelect, selectedCode }) => {
  return (
    <MenusBox>
      {_map(codes, c => {
        const selected = c.id === selectedCode.id;
        return (
          <Button
            primary={selected}
            basic={!selected}
            size={"medium"}
            active={selected}
            style={{ marginBottom: 40 }}
            onClick={() => onSelect(c)}
            key={c.id}
          >
            {c.name}
          </Button>
        );
      })}
    </MenusBox>
  );
};

const ContentBox = styled.div`
  flex: 1;
`;

const RightBox = styled.div`
  width: 180px;
`;

const AboutPage = ({ aboutCodes }) => {
  const [selectedCode, setSelectedCode] = useState(_head(aboutCodes));
  if (!selectedCode) {
    return <Empty text={"没有关于哦！"} />;
  }
  return (
    <Container>
      <PageTitle title={"关于"} metaKeywords={"关于LazyChun"} />
      <Menus
        codes={aboutCodes}
        selectedCode={selectedCode}
        onSelect={code => setSelectedCode(code)}
      />
      <ContentBox>
        <Blog id={selectedCode.value} />
      </ContentBox>
      <RightBox />
    </Container>
  );
};

export default AboutPage;
