import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { getDataFromUrl } from "../../../utils/urlData";
import { AppContext } from "../../../../App";

const Container = styled.div`
  margin-top: 10px;
  text-align: left;
  padding-bottom: 20px;
  font-size: 16px;
  img {
    object-fit: contain;
    max-width: 100%;
  }
`;

const _Article = ({ contentUrl, setGlobalBgColor, globalBg }) => {
  const [content, setContent] = useState("");
  useEffect(() => {
    if (globalBg) {
      setGlobalBgColor(globalBg);
    }

    return () => setGlobalBgColor("white");
  }, []);
  useEffect(() => {
    getDataFromUrl(contentUrl, "data.content", str => setContent(str));
  }, [contentUrl]);
  return (
    <Container>
      <div dangerouslySetInnerHTML={{ __html: content }} />
    </Container>
  );
};

const Article = ({ contentUrl, globalBg }) => {
  return (
    <AppContext.Consumer>
      {props => {
        const { setGlobalBgColor } = props;
        return (
          <_Article
            contentUrl={contentUrl}
            globalBg={globalBg}
            setGlobalBgColor={setGlobalBgColor}
          />
        );
      }}
    </AppContext.Consumer>
  );
};

export default Article;
