import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { getRouteId } from "../../utils/routeUtils";
import { GET_BLOG } from "../../common/queryGql";
import PageItem from "../QueryItem";
import BlogContent from "../blog/BlogSegaments/BlogContent";
import BlogComments from "../blog/BlogSegaments/BlogComments";
import LoadingWrapper from "../../common/LoadingWapper";
import { useRead } from "../../common/hooks";
import { OBJ_TYPES } from "../../common/CONSULT";

const Container = styled.div`
  width: 100%;
`;

const Blog = ({ id }) => {
  useRead({ objId: id, objType: OBJ_TYPES.BLOG });
  return (
    <PageItem autoLoading id={id} itemPath={"blog"} gql={GET_BLOG}>
      {({ loading, refetch, item }) => {
        return (
          <LoadingWrapper loading={loading}>
            <Container>
              <BlogContent blog={item} refetchBlog={refetch} />
              <BlogComments blogId={id} />
            </Container>
          </LoadingWrapper>
        );
      }}
    </PageItem>
  );
};

export default Blog;
