import React, { useState, useEffect } from "react";
import styled from "styled-components";

const Container = styled.h1`
  color: #404040;
  font-weight: 700;
  width: 100%;
  margin-top: 30px;
  margin-bottom: 10px;
`;

const Title = ({ title }) => {
  return <Container>{title}</Container>;
};

export default Title;
