import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Author from "./Author";
import InfoItem from "../../../../common/segaments/InfoItem";
import { SWITCH_FOLLOW, SWITCH_UP } from "../../../../common/mutationGql";
import { OBJ_TYPES } from "../../../../common/CONSULT";
import ClickWrapper from "../../../ClickWrapper";
import Space from "../../../../common/Space";
import ReportButton from "../../../../common/ReportButton";

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f9f9f9;
  padding: 5px 20px;
  .report {
    display: none;
  }
  &:hover {
    .report {
      display: block;
    }
  }
`;

const ReportBox = styled.div``;

const ReportBtn = styled.div`
  color: #cecece;
  cursor: pointer;
`;

const LeftBox = styled.div`
  display: flex;
  align-items: center;
`;

const BasicInfo = ({ blog, refetchBlog }) => {
  const {
    id,
    creator,
    lastUpdated,
    dateCreated,
    upCount,
    currentUpped,
    followerCount,
    currentFollowed,
    readCount,
    currentReport,
    title
  } = blog;
  return (
    <Container>
      <LeftBox>
        <Author creator={creator} />
        <Space width={20} />
        <InfoItem
          hintText={"创建日期"}
          icon={"calendar alternate outline"}
          text={dateCreated}
        />
        <Space width={20} />
        <ClickWrapper
          gql={SWITCH_UP}
          variables={{ objId: id, objType: OBJ_TYPES.BLOG }}
          onCompleted={() => refetchBlog()}
        >
          <InfoItem
            iconStyle={{ color: currentUpped ? "#F76976" : "" }}
            hintText={currentUpped ? "取消喜欢" : "点击喜欢"}
            icon={currentUpped ? "heart" : "heart outline"}
            text={upCount}
          />
        </ClickWrapper>
        <Space width={20} />
        <ClickWrapper
          gql={SWITCH_FOLLOW}
          variables={{ objId: id, objType: OBJ_TYPES.BLOG }}
          onCompleted={() => refetchBlog()}
        >
          <InfoItem
            iconStyle={{ color: currentFollowed ? "#F76976" : "" }}
            hintText={currentFollowed ? "取消收藏" : "点击收藏"}
            icon={currentFollowed ? "star" : "star outline"}
            text={followerCount}
          />
        </ClickWrapper>
        <Space width={20} />
        <InfoItem
          icon={"eye"}
          text={Number(readCount) + 1}
          hintText={"阅读次数"}
        />
      </LeftBox>
      <ReportBox className={"report"}>
        <ReportButton
          reportCallback={() => refetchBlog()}
          objId={id}
          currentReport={currentReport}
          type={OBJ_TYPES.BLOG}
          title={`举报博客【${title}】`}
        >
          <ReportBtn>{currentReport ? "已举报" : "举报"}</ReportBtn>
        </ReportButton>
      </ReportBox>
    </Container>
  );
};

export default BasicInfo;
