import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Title from "./blogContent/Title";
import BasicInfo from "./blogContent/BasicInfo";
import Article from "./Article";
import Video from "./Video";

const Container = styled.div`
  background-color: ${props => (props.isVideo ? "rgba(0,0,0,0.1)" : "white")};
  width: 100%;
  min-height: 870px;
  padding-top: ${props => (props.isVideo ? "10" : "22")}px;
  padding-left: ${props => (props.isVideo ? "0" : 20)}px;
  padding-right: ${props => (props.isVideo ? "0" : 20)}px;
  border-radius: 3px;
`;

const BlogContent = ({ blog, refetchBlog, articleGlobalBg }) => {
  const { title, creator, type, upCount, contentUrl } = blog;
  const isVideo = type === "Video";
  return (
    <Container isVideo={isVideo}>
      <Title title={title} isVideo />
      <BasicInfo blog={blog} refetchBlog={refetchBlog} />
      {type === "Article" ? (
        <Article contentUrl={contentUrl} globalBg={articleGlobalBg} />
      ) : (
        <Video contentUrl={contentUrl} />
      )}
    </Container>
  );
};

export default BlogContent;
