import React from "react";
import { Icon, Popup } from "semantic-ui-react";
import styled from "styled-components";

const InfoItemBox = styled.div`
  display: flex;
  margin-left: 20px;
  cursor: pointer;
`;

const ItemText = styled.div``;

const InfoItem = ({
  icon,
  mf,
  boxStyle,
  iconStyle,
  text,
  hintText,
  onClick
}) => {
  const content = (
    <InfoItemBox onClick={onClick} style={{ ...boxStyle, marginLeft: mf }}>
      <Icon style={iconStyle} name={icon} />
      <ItemText>{text}</ItemText>
    </InfoItemBox>
  );
  return hintText ? <Popup content={hintText} trigger={content} /> : content;
};

export default InfoItem;
